<template>
  <div>
    <group-a></group-a>
    <group-b></group-b>
    <span v-for="kişi in docs" v-bind:key="kişi._id">{{ kişi.name }} | </span>
  </div>
</template>

<script>
import groupA from '@/components/groupA'
import groupB from '@/components/groupB'
import EventService from '@/services/EventService'

export default {
  components: {
    groupA,
    groupB
  },
  data() {
    return {
      docs: []
    }
  },
  created() {
    EventService.getAll()
      .then(res => {
        this.docs = res.data
      })
      .catch(error => {
        console.log(error)
      })
  }
}
</script>

<style scoped></style>
